import React, {Component} from "react";
import PropTypes from "prop-types";
import {OraButton, OraLoader, OraMessage} from "eshop/components/OraCommonComponents";
import {connect} from "react-redux";
import {LabeledInputWithErrors, LabeledPostalCodeInputWithErrors} from "eshop/modules/core/components/forms";
import {LabeledAutocomplete} from "eshop/modules/core/components/ui/Autocomplete";

export default class MulticartMnpBusinessAddressComponent extends Component {
    constructor(props) {
        super(props);
    }

    getPropsForInput(name) {
        return {
            name,
            value: this.props.entry[name] || "",
            errors: this.props.entry.errors && this.props.entry.errors[name],
            onChange: this.onValueChange.bind(this)
        };
    }

    onValueChange(target) {
         this.props.changeCustomerMnpDataFormField({...target, entryIndex: this.props.entryIndex, defaults: this.props.mnpFormData})
    }

    render() {
        return (
            <div className="opl-form">
                <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                    <LabeledPostalCodeInputWithErrors label="Kod pocztowy" {...this.getPropsForInput("postalCode")} labelType="floating"/>
                </div>

                <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                    <LabeledInputWithErrors label="Miejscowość" {...this.getPropsForInput("city")} labelType="floating"/>
                </div>

                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12">
                    <div className="u-spacing-l "/>
                    <LabeledInputWithErrors label="Ulica" {...this.getPropsForInput("street")} labelType="floating"/>
                    <div className="u-spacing-l "/>
                </div>

                <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                    <LabeledInputWithErrors label="Nr domu" {...this.getPropsForInput("houseNumber")} labelType="floating"/>
                </div>

                <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 ">
                    <LabeledInputWithErrors label="Nr lokalu" {...this.getPropsForInput("flatNumber")} labelType="floating"/>
                </div>

            </div>

        )
    }
}
